'use client';

import React, { useState } from 'react';
import useSWR from 'swr';
import {
    Calendar as CalendarIcon, Clock, Users, Phone,
    MapPin, CheckCircle, XCircle, Plus
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

export default function ReservationsPage() {
    const { data: reservationsData, mutate } = useSWR('/api/reservations', fetcher);
    const reservations = reservationsData?.data || [];

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [formData, setFormData] = useState({
        customerName: '',
        phone: '',
        bookingDate: new Date().toISOString().split('T')[0],
        bookingTime: '19:00',
        pax: 2,
        tableType: 'REGULAR',
        notes: ''
    });

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            await fetch('/api/reservations', {
                method: 'POST',
                body: JSON.stringify(formData)
            });
            mutate();
            setIsModalOpen(false);
        } catch (e) {
            alert('Failed to create reservation');
        }
    };

    return (
        <div className="flex flex-col h-full bg-[#050a07] p-8 overflow-y-auto">
            <div className="flex justify-between items-center mb-8">
                <div>
                    <h2 className="text-2xl font-bold text-white mb-2">Reservations</h2>
                    <p className="text-slate-400">Manage table bookings</p>
                </div>
                <button
                    onClick={() => setIsModalOpen(true)}
                    className="bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold py-2.5 px-4 rounded-lg flex items-center gap-2 transition-colors"
                >
                    <Plus size={20} /> New Booking
                </button>
            </div>

            <div className="space-y-4">
                {reservations.length === 0 ? (
                    <div className="text-center py-20 bg-[#0f1a14] rounded-xl border border-[#1e3328] text-slate-500">
                        <CalendarIcon size={48} className="mx-auto mb-4 opacity-50" />
                        <p>No upcoming reservations</p>
                    </div>
                ) : (
                    reservations.map((res: any) => (
                        <div key={res.id} className="bg-[#0f1a14] border border-[#1e3328] hover:border-[#22c55e]/50 rounded-xl p-6 transition-all group flex items-start gap-6">
                            <div className="bg-[#16261d] p-4 rounded-lg text-center min-w-[80px]">
                                <span className="block text-xs font-bold text-slate-400 uppercase tracking-widest mb-1">
                                    {new Date(res.bookingDate).toLocaleDateString('en-US', { month: 'short' })}
                                </span>
                                <span className="block text-3xl font-bold text-white mb-1">
                                    {new Date(res.bookingDate).getDate()}
                                </span>
                                <span className="block text-xs text-[#22c55e] font-mono bg-[#22c55e]/10 py-1 rounded">
                                    {new Date(res.bookingTime).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' })}
                                </span>
                            </div>

                            <div className="flex-1">
                                <div className="flex justify-between items-start mb-2">
                                    <h3 className="font-bold text-xl text-white">{res.customerName}</h3>
                                    <span className="bg-yellow-500/20 text-yellow-500 text-xs font-bold px-2 py-1 rounded">
                                        {res.status}
                                    </span>
                                </div>

                                <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 text-sm text-slate-400 mb-4">
                                    <span className="flex items-center gap-2"><Phone size={14} /> {res.phone}</span>
                                    <span className="flex items-center gap-2"><Users size={14} /> {res.pax} Guests</span>
                                    <span className="flex items-center gap-2"><MapPin size={14} /> {res.tableType} Area</span>
                                    {res.notes && <span className="flex items-center gap-2 italic">"{res.notes}"</span>}
                                </div>
                            </div>

                            <div className="flex flex-col gap-2">
                                <button className="p-2 hover:bg-[#22c55e]/20 text-slate-400 hover:text-[#22c55e] rounded transition-colors" title="Confirm">
                                    <CheckCircle size={20} />
                                </button>
                                <button className="p-2 hover:bg-red-500/20 text-slate-400 hover:text-red-500 rounded transition-colors" title="Cancel">
                                    <XCircle size={20} />
                                </button>
                            </div>
                        </div>
                    ))
                )}
            </div>

            {/* Modal */}
            {isModalOpen && (
                <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/80 backdrop-blur-sm p-4">
                    <div className="bg-[#0f1a14] border border-[#1e3328] rounded-2xl w-full max-w-lg p-6 shadow-2xl relative">
                        <h3 className="text-xl font-bold text-white mb-6">New Reservation</h3>
                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div className="grid grid-cols-2 gap-4">
                                <input
                                    placeholder="Check-in Date"
                                    type="date"
                                    required
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.bookingDate}
                                    onChange={e => setFormData({ ...formData, bookingDate: e.target.value })}
                                />
                                <input
                                    placeholder="Time"
                                    type="time"
                                    required
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.bookingTime}
                                    onChange={e => setFormData({ ...formData, bookingTime: e.target.value })}
                                />
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <input
                                    placeholder="Customer Name"
                                    required
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.customerName}
                                    onChange={e => setFormData({ ...formData, customerName: e.target.value })}
                                />
                                <input
                                    placeholder="Phone"
                                    required
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.phone}
                                    onChange={e => setFormData({ ...formData, phone: e.target.value })}
                                />
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <input
                                    placeholder="Pax"
                                    type="number"
                                    min="1"
                                    required
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.pax}
                                    onChange={e => setFormData({ ...formData, pax: Number(e.target.value) })}
                                />
                                <select
                                    className="bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none"
                                    value={formData.tableType}
                                    onChange={e => setFormData({ ...formData, tableType: e.target.value })}
                                >
                                    <option value="REGULAR">Regular Table</option>
                                    <option value="VIP">VIP Table</option>
                                </select>
                            </div>
                            <textarea
                                placeholder="Notes"
                                className="w-full bg-[#050a07] border border-[#1e3328] rounded px-4 py-3 text-white focus:border-[#22c55e] outline-none h-24 resize-none"
                                value={formData.notes}
                                onChange={e => setFormData({ ...formData, notes: e.target.value })}
                            ></textarea>

                            <div className="flex gap-3 pt-4">
                                <button
                                    type="button"
                                    onClick={() => setIsModalOpen(false)}
                                    className="flex-1 py-3 text-slate-400 font-bold hover:text-white transition-colors"
                                >
                                    Cancel
                                </button>
                                <button
                                    type="submit"
                                    className="flex-1 py-3 bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold rounded shadow-lg shadow-[#22c55e]/20 transition-colors"
                                >
                                    Confirm Booking
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}
        </div>
    );
}
